html {
  background-image: linear-gradient(#214e6e, #347AAD);
  height:fit-content;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

header h1 {
  font-size: 100px;
  color: rgb(94, 145, 204);
}

.navigator {
  width: 400px;
  display: flex;
  justify-content:space-evenly;
  align-items: center;
}

.Link-to {
  text-decoration: none;
  color:#91BEDE;
  padding: 20px;
}

.Link-to:hover {
  color: lightgrey;
}

nav hr {
  height: 20px;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-hr{
  height: 100px;
  padding: 10px;
  background-color: rgb(94, 145, 204);
  border: 1px black;
}

.section-habilidades {
  width: 900px;
  display: flex;
  padding: 20px;
  color: lightgray;
  position: relative;
  animation: mymove 3s ease-in-out;
}

@keyframes mymove {
  from {right: 200px;}
  to {right: 0px;}
}

.between-sections {
  width: 1000px;
  background-color: black;
  color: black;
}

.section-html-css div{
  padding: 20px;
}

.html-css-icons {
  width: 200px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.java-script-icon {
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#react-logo {
  width: 80px;
}

.react-boots-icon {
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.identity-section {
  margin-bottom: 20px;
}

.identity-section p {
  color: lightgray;
  font-weight: bold;
}

.gabriel-photo {
  width: 250px;
  border-radius: 120px;
  border: 5px solid lightgray;
}

.about-me {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ab-sections {
  position: relative;
  animation: absection 3s ease-in-out;
}

@keyframes absection {
  from {right: 200px;}
  to {right: 0px;}
}

.about-me h2 {
  color: lightgray;
}

.about-me  p{
  width: 800px;
  color: lightgray;
  text-align: justify;
  position: relative;
  animation: aboutmemove 3s ease-in-out;
}

@keyframes aboutmemove {
  from {right: 200px;}
  to {right: 0px;}
}

.contato h2 {
  color: lightgray;
}

.projects-class {
  color: lightgray;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-class ul{
  position: relative;
  animation: projectsmove 3s ease-in-out;
}

@keyframes projectsmove {
  from {right: 100px;}
  to {right: 0px;}
}

.projects-div {
  display: flex;
  align-items: center;
}

.projects-div div {
  padding: 20px;
}

.projects-div p {
  text-align: center;
  font-weight: bold;
}

.projects-div img{
  width: 700px;
  border-radius: 100px;
  border: 2px solid lightgray;
  padding: 20px;
}

.projects-div img:hover {
  opacity: 0.5;
}
